<template>
  <div class="page-x">
    <!-- 1  -->
    <h2></h2>
    <!--    <div class="g6-x" id="containerG6" ref="containerG6"></div>-->
    <div id="container"></div>
  </div>
</template>

<script>
// G6  main.js
import G6 from '@antv/g6'

export default {
  name: "Gtry",
  data(){
    return{
      treeData : {
        id:'',
        label:'',
        children:[
          {
            id:'1.',
            label:'1.',
            children:[
              {
                id:'1.1',
                label:'1.1',
              },
              {
                id:'1.2',
                label:'1.2',
              }
            ]
          },
          {
            id:'2.',
            label: '2.',
            children: [
              {
                id:'2.1',
                label: '2.1',
              },
              {
                id:'2.2',
                label: '2.2',
              }
            ]
          }
        ]
      }
    }
  },
  methods: {
    initTree(treeData){
      const width = document.getElementById('container').scrollWidth || 800;
      const height = document.getElementById('container').scrollHeight || 800;
      const graph = new G6.TreeGraph({
        container: 'container',
        width,
        height,
        fitView: true,
        fitCenter: true,
        maxZoom: 1.5,
        linkCenter: true,
      //  
        modes: {
          default: [
            {
              // 
              type: 'collapse-expand',
              // trigger 'click'  'dblclick'  'dblclick'
              trigger: 'click',
              // onChangeV3.1.2 
              onChange: function onChange(item, collapsed) {
                const data = item.get('model').data;
                data.collapsed = collapsed;
                return true;
              }
            },
            // 
            'drag-canvas',
            // 
            'zoom-canvas',
          ]
        },

        //  type, size, color data 
        defaultNode: {
          type: 'circle',
          // size 
          size: 26,
          // : [0, 0][1, 1],
          anchorPoints: [
            [0, 0.5],
            [1, 0.5],
          ],
          // 
          style: {
            // 
            fill: '#C6E5FF',
            // ''
            stroke: '#5B8FF9',
            // 
            cursor:'pointer',
          },
          // Object 
          labelCfg: {
            // 
            style: {
              // 
              fill: '#000000',
              // 
              fontSize: 14,
            },
          },
        },
        //  type, size, color data 
        defaultEdge: {
          //  'line'
          type: 'cubic-vertical',
          // 
          style: {
            // 
            stroke: '#A3B1BF',
          },
        },
        //  type 
        layout: {
          // 
          type: 'compactBox',
          // layout 
          direction: 'TB', // H / V / LR / RL / TB / BT
          // 
          //  id 
          getId: function getId(d) {
            return d.id;
          },
          // 
          getHeight: function getHeight() {
            return 16;
          },
          // 
          getWidth: function getWidth() {
            return 16;
          },
          // 
          getVGap: function getVGap() {
            return 40;
          },
          // 
          getHGap: function getHGap() {
            return 70;
          },
        },
        // 
        animate: true,
      });
      //  KeyShape 
      //  render 
      //  graph.node(nodeFn)  >  > 
      graph.node(function (node) {
        return {
          label: node.label,
          labelCfg: {
            position: node.children && node.children.length > 0 ? 'top' : 'bottom',
            offset: 5,
            style: {
              textAlign: 'center',
            },
          },
        };
      });
      // 
      graph.data(treeData);
      // 
      graph.render();
      // 
      graph.fitView();
    }
  },

  mounted() {
    this.initTree(this.treeData)
  }
}
</script>

<style scoped>
.g6-x {
  width: 600px;
  height: 500px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-left: 20px;
}
.container{
  width: 100%;
  height: 800px;
}
</style>