<template>
  <div>
    <v-alert
        class="alert"
        outlined
        type="success"
        text
        v-show="showSuccessDialog"
        transition="scroll-y-transition"
    >
      
    </v-alert>

    <!--    -->
    <v-alert
        class="alert"
        outlined
        type="warning"
        text
        v-show="showFailDialog"
        transition="scroll-y-transition"
    >

      <v-dialog v-model="dialog" width="500">
        <v-card>
          <v-card-title>
            
          </v-card-title>
          <v-card-text>
            <!--            -->
            <!--            <v-select-->
            <!--                v-model="labelInfo.ability"-->
            <!--                :items="abilities"-->
            <!--                label=""-->
            <!--                outlined-->
            <!--            ></v-select>-->
            <v-select
                v-model="labelInfo.device"
                :items="devices"
                label=""
                outlined
            ></v-select>
            <!--            <v-select-->
            <!--                v-model="labelInfo.activity"-->
            <!--                :items="activities"-->
            <!--                label=""-->
            <!--                outlined-->
            <!--            ></v-select>-->
            <v-select
                v-model="labelInfo.taskType"
                :items="taskTypes"
                label=""
                outlined
            ></v-select>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" text @click="handleChange">
              
            </v-btn>
            <v-btn color="primary" text @click="dialog = false">
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-alert>


    <v-container class="pl-16 pr-16 mt-16">
      <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content">
            <el-row class="mt-9"></el-row>
            <img
                class="picture"
                style="width: 200px; height: 200px"
                src="../../assets/employee.png"
            >
          </div>
        </el-col>

        <el-col :span="16">
          <div class="grid-content">
            <el-descriptions title="" direction="vertical" :column="4" border>
              <el-descriptions-item label="ID">
                <template slot="label">
                  <i class="el-icon-user"></i>
                  ID
                </template>
                {{userInfo.id}}
              </el-descriptions-item>
              <el-descriptions-item>
                <template slot="label">
                  <i class="el-icon-user"></i>
                  
                </template>
                {{userInfo.uname}}
              </el-descriptions-item>
              <el-descriptions-item label="" :span="2">{{userInfo.phone}}</el-descriptions-item>
              <el-descriptions-item label="">
                <el-tag size="small">{{userInfo.userRole}}</el-tag>
              </el-descriptions-item>
              <el-descriptions-item label="">{{userInfo.createTime}}</el-descriptions-item>
            </el-descriptions>

            <br/>

            <el-descriptions class="margin-top" title="" :column="1" border>
              <template slot="extra">
                <el-button
                    type="primary"
                    size="small"
                    @click="showDialog"
                    dark
                    class="deep-purple">
                  
                </el-button>
              </template>
              <!--        <el-descriptions-item label="">-->
              <!--          {{labelInfo.ability}}-->
              <!--        </el-descriptions-item>-->
              <!--        <el-descriptions-item label="">-->
              <!--          {{labelInfo.device}}-->
              <!--        </el-descriptions-item>-->
              <!--        <el-descriptions-item label="">-->
              <!--          {{labelInfo.activity}}-->
              <!--        </el-descriptions-item>-->
              <!--        <el-descriptions-item label="">-->
              <!--          {{labelInfo.taskType}}-->
              <!--        </el-descriptions-item>-->
            </el-descriptions>
          </div>
        </el-col>
      </el-row>
    </v-container>

    <v-container class="pl-16 pr-16">

    </v-container>

    <!--    <v-container class="pl-16 pr-16 mb-16">-->
    <!--      <el-descriptions class="margin-top" title="" :column="2" border>-->
    <!--        <el-descriptions-item label="">-->
    <!--          {{systemLabelInfo.recommendation}}-->
    <!--        </el-descriptions-item>-->
    <!--        <el-descriptions-item label="">-->
    <!--          {{systemLabelInfo.preference}}-->
    <!--        </el-descriptions-item>-->
    <!--      </el-descriptions>-->
    <!--    </v-container>-->
  </div>
</template>

<script>
import { getUser,choosePreference } from "@/api/user";

export default {
  name: "UserCenter",

  data() {
    return {
      userInfo: {
        id: 0,
        uname: "",
        phone: "",
        password: null,
        picture: null,
        balance: 0,
        userRole: "",
        createTime: ""
      },
      //  
      labelInfo:{
        // ability:"",
        device:"Android",
        // activity:"",
        taskType:""
      },
      //    
      // abilities:["","","","","","","",""],
      devices:["Android","Linux","","ios"],
      // activities:["","","",""],
      taskTypes: ["", ""],

      //  
      // systemLabelInfo:{
      //   recommendation:"",
      //   preference:""
      // },
      dialog: false,
      showSuccessDialog: false,
      showFailDialog: false,
      msg: "",
      showAlert: false,
      alertMsg: "",
      value: 0,
    };
  },

  methods: {
    refreshUserInfo() {
      const userId = window.localStorage.getItem("userId");
      getUser(userId).then(res => {
        this.userInfo = res || {};
      });
    },

    // refreshLabelInfo(){
    //   const userId = window.localStorage.getItem("userId");
    //   getUserLabel(userId).then(res => {
    //     this.labelInfo = res || {};
    //   });
    // },

    showDialog(){
      this.dialog = true;
    },

    //  payload{uid,labelInfo}
    // 1 
    handleChange(){
      this.dialog = false
      const payload = {
        ...this.labelInfo,
        // id: 1,
        employeeId: window.localStorage.getItem("userId"),
      };
      console.log(payload);
      // var qs = require('qs');
      // var payload1 = qs.stringify(payload)
      // console.log(payload1)
      choosePreference(payload).then(res =>{
        console.log(res);
        if (res.code === 1){
          this.showSuccessDialog = true;
          // this.refreshLabelInfo();
          setTimeout(()=>{
            this.showSuccessDialog = false;
          }, 1000);
        }else {
          console.log(res);
          this.showFailDialog = true;
          this.msg = res.msg;
          // this.msg = ""
          // this.refreshLabelInfo();
          setTimeout(() => {
            this.showFailDialog = false;
          }, 1000);
        }
      })
    },

    // refreshSystemLabelInfo(){
    //   const userId = window.localStorage.getItem("userId");
    //   getUserSystemLabel(userId).then(res => {
    //     this.systemLabelInfo = res || {};
    //   })
    // }
  },

  mounted() {
    this.refreshUserInfo();
    // this.refreshLabelInfo();
    // this.refreshSystemLabelInfo();
  }
};
</script>

<style scoped>
.alert {
  position: fixed;
  left: 50%;
  top: 100px;
  z-index: 999;
}
</style>
