<template>
  <div>
    <v-container class="pl-16 pr-16">
      <div style="margin: 60px;" class="pa-12 grey lighten-5 mt-8">
        <el-form
            ref="form"
            :model="form"
            :rules="rules"
            label-width="110px"
            :label-position="labelPosition"
        >
          <el-form-item label="" prop="title">
            <el-input v-model="form.title"></el-input>
          </el-form-item>

          <el-form-item label="" prop="bugForm">
            <el-select v-model="form.bugForm" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="severity">
            <el-select v-model="form.severity" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="recurrence">
            <el-select v-model="form.recurrence" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="device">
            <el-select v-model="form.device" placeholder="">
              <el-option label="Android" value="Android"></el-option>
              <el-option label="Linux" value="Linux"></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <!--          <el-form-item label="" prop="#">
                        <v-file-input
                                v-model="instructionFile"
                                label=""
                                @change="handleUploadImage"
                        ></v-file-input>
                    </el-form-item>-->

          <!--                      :http-request="upload"
                                :before-upload="beforeAvatarUpload"-->

          <el-form-item label="" prop="#">
            <el-upload
                show-file-list
                drag
                action="#"
                :http-request="handleUploadImage"
                :file-list="fileList"
                list-type="picture"
                :limit="1"
                accept=".jpg,.png"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text"><em></em></div>
              <div class="el-upload__tip" slot="tip">jpg/png,1</div>
            </el-upload>
          </el-form-item>

          <el-form-item label="" prop="intro">
            <el-input
                type="textarea"
                autosize
                placeholder=""
                v-model="form.intro" >
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="steps">
            <el-input
                type="textarea"
                autosize
                placeholder=""
                v-model="form.steps">
            </el-input>
          </el-form-item>
          <el-row type="flex" class="row-bg" justify="space-around">
            <el-button type="primary" @click="submitForm('form')"></el-button>
            <el-button type="warning" @click="resetForm('form')"></el-button>
            <el-button @click="goBack()"></el-button>
          </el-row>
        </el-form>
      </div>
    </v-container>
  </div>
</template>

<!---->
<script>
import { saveReport,getOldReport } from "@/api/report";
import {oss} from "@/api/file";

export default {
  name: "PostEdit",
  data() {
    return {
      labelPosition: 'right',
      form: {
        title: '',
        bugForm: '',
        severity: '',
        recurrence: '',
        device: '',
        picture: '',
        intro: '',
        steps: '',
        id: '',
        reportTime: ''
      },
      rules:{
        title:[
          { required: true, message:"", trigger:'blur'},
          { min: 2, max: 15, message: "215", trigger: 'blur'}
        ],
        intro:[
          { required: true, message:"", trigger:'blur'}
        ],
        steps:[
          { required: true, message:"", trigger:'blur'}
        ],
      },
      dialogFormVisible: false,
      fileList: [],
      instructionFile: '',
      reportId: null,
    }
  },
  methods: {
    async handleUploadImage(file) {
      const formData = new window.FormData();
      formData.append("file", file.file);
      const res = await oss(formData);
      console.log(res);
      //if (res.code === 1) {
      this.form.picture = res.url;
      //}
    },
    // 
    goBack() {
      window.history.back();
    },

    submitForm(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          const payload = {
            employeeName: window.localStorage.getItem("username"),
            employeeId: window.localStorage.getItem("userId"),
            taskId: this.$route.params.taskId,
            title: this.form.title,
            intro: this.form.intro,
            steps: this.form.steps,
            picture: this.form.picture,
            bugForm: this.form.bugForm,
            severity: this.form.severity,
            recurrence: this.form.recurrence,
            device: this.form.device,
            id: this.form.id,
            reportTime: this.form.reportTime
          };
          console.log(payload);
          saveReport(payload).then(res => {
            console.log(res);
            this.snackbar = true;
            this.reportId = res.data.id;
          })
          alert('submit!');
          // alert("{{res.msg}}")
          this.resetForm(form);
          setTimeout(() => {
            const taskId = this.$route.params.taskId;
            this.$router.push(`/employee/similar/${taskId}/${this.reportId}/0`);
          }, 1000);
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // resetField	  
    resetForm(form) {
      this.$refs[form].resetFields();
    },
    // submitUpload() {
    //   this.$refs.upload.submit();
    // },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    loadReport(){
      const payload={
        employeeId: window.localStorage.getItem("userId"),
        taskId: this.$route.params.taskId
      }
      console.log(payload);
      getOldReport(payload).then(res =>{
        console.log(res);
        if(res.code==1){
          this.form=res.data;
        }
      })
    }
  },
  mounted() {
    this.loadReport();
  }
};
</script>

<style>
.row-bg{
  padding: 10px 0;
}
</style>