<template>
  <v-parallax
      dark
      height = "760"
      src="https://w.wallhaven.cc/full/yj/wallhaven-yjw53l.jpg"
  >
    <div>
      <v-container class="box">
        <!-- alert -->
        <v-alert
            class="alert"
            outlined
            type="success"
            text
            v-show="showSuccessDialog"
            transition="scroll-y-transition"
        >
          {{ username }} !
        </v-alert>
        <!-- alert -->
        <v-alert
            class="alert"
            outlined
            type="warning"
            text
            v-show="showFailDialog"
            transition="scroll-y-transition"
        >
          {{ msg }}
        </v-alert>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :model="form" :label-position="labelPosition">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.device" placeholder="">
                <el-option label="Android" value="Android"></el-option>
                <el-option label="Linux" value="Linux"></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="ios" value="ios"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.taskType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="handleChange"> </el-button>
          </div>
        </el-dialog>

        <el-form :model="register" class="pa-12 grey lighten-5 mt-10 form">
          <el-form-item
                  prop="username"
                  :rules="{ required: true, message: ''}"
          >
              <el-input v-model="register.username" placeholder=""></el-input>
          </el-form-item>
            <el-form-item
                    prop="phone"
                    :rules="[
                    { required: true, message: ''},
                    { type: 'number', message: ''}
                    ]"
            >
                <el-input type="age" v-model.number="register.phone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item
                    prop="password"
                    :rules="{ required: true, message: ''}"
            >
                <el-input v-model="register.password" placeholder="" show-password></el-input>
            </el-form-item>
            <el-form-item>
                <v-select v-model="userRole" :items="roles" label=""></v-select>
                <v-btn class="ml-0 ma-4 info" @click="handleRegister">
                    
                </v-btn>
                <v-btn text color="primary" class="float-right ma-4" @click="() => {this.$router.push('/login');}">
                    
                </v-btn>
            </el-form-item>

        </el-form>
      </v-container>
    </div>
  </v-parallax>
</template>

<script>
import { register,choosePreference } from "@/api/user";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Register",
  data() {
    return {
      register:{
          username: "",
          phone: "",
          password: "",
      },
      userRole: "employee",
      showSuccessDialog: false,
      showFailDialog: false,
      msg: "",
      roles: ["employer", "employee", "admin"],
      dialogFormVisible: false,
      form:{
        taskType:'',
        device:"Android"
      },
      formLabelWidth: '200px',
      labelPosition: 'top',
      uid:0,
    };
  },
  methods: {
    handleRegister() {
      register({
        uname: this.register.username,
        phone: this.register.phone,
        password: this.register.password,
        userRole: this.userRole.toUpperCase()
      }).then(res => {
        console.log(res);
        if (res.code === 1) {
          if (res.data.userRole==="EMPLOYEE"){
            this.uid = res.data.id;
            this.dialogFormVisible = true;
          } else {
            window.localStorage.setItem("userPhone", this.register.phone);
            window.localStorage.setItem("userPassword", this.register.password);
            this.register.username = res.data.uname;
            this.uid = res.data.id;
            this.showSuccessDialog = true;
            setTimeout(() => {
              this.showSuccessDialog = false;
            }, 500);
            setTimeout(() => {
              this.$router.push("/login");
            }, 1000);
          }
        } else {
          this.msg = res.msg;
          this.showFailDialog = true;
          setTimeout(() => {
            this.showFailDialog = false;
          }, 1000);
        }
      });
    },

    handleChange(){
      this.dialogFormVisible = false;
      console.log(this.uid)
      const payload = {
        ...this.form,
        employeeId: this.uid,
      };
      console.log(payload);
      choosePreference(payload).then(res =>{
        console.log(res);
        if (res.code === 1){
          this.$router.push("/login");
        }
        else {
          alert('');
        }
      })
    },
  }
};
</script>

<style scoped>
.form {
  position: relative;
  top: 50px;
}
.alert {
  position: fixed;
  left: 50%;
  top: 100px;
  z-index: 999;
}
.box {
  margin-bottom: 15%;
  width: 60%;
  opacity: 0.9;
}
</style>